'use client';

import { useState } from 'react';
import { Button } from '@/components/ui/button';
import { Card } from '@/components/ui/card';
import { AlertDialog, AlertDialogContent, AlertDialogTrigger } from '@/components/ui/alert-dialog';
import { Textarea } from '@/components/ui/textarea';

export default function HomePage() {
  const [query, setQuery] = useState('');
  const [documents, setDocuments] = useState<string[]>([]);
  const [answer, setAnswer] = useState('');
  const [error, setError] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [history, setHistory] = useState<string[]>([]);

  const handleQuerySubmit = async () => {
    if (!query.trim()) return;

    setIsLoading(true);
    try {
      // Simulate API call delay
      await new Promise(resolve => setTimeout(resolve, 1000));

      // Add to history
      setHistory(prev => [query, ...prev]);
      
      // Simulate retrieval
      setDocuments([
        'Document 1: This is a relevant passage about ' + query,
        'Document 2: Here is another context about ' + query,
        'Document 3: Additional information related to ' + query
      ]);

      // Simulate answer generation
      setAnswer('This is a generated answer based on your query: ' + query);
      
      // Clear error if any
      setError('');
    } catch (err) {
      console.error(err);
      setError('Failed to process the query. Please try again.');
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-b from-gray-50 to-white">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
        <div className="grid grid-cols-1 md:grid-cols-4 gap-8">
          {/* Sidebar: Query History */}
          <aside className="md:col-span-1">
            <Card className="p-6 bg-white shadow-lg rounded-xl border-0">
              <h2 className="text-xl font-bold mb-6 text-gray-900">History</h2>
              <div className="space-y-4">
                {history.length > 0 ? (
                  <ul className="space-y-3">
                    {history.map((item, index) => (
                      <li
                        key={index}
                        className="p-3 bg-gray-50 hover:bg-gray-100 rounded-lg cursor-pointer transition-colors duration-200 text-sm text-gray-700"
                        onClick={() => setQuery(item)}
                      >
                        {item}
                      </li>
                    ))}
                  </ul>
                ) : (
                  <p className="text-gray-500 text-sm italic">No queries yet</p>
                )}
              </div>
            </Card>
          </aside>

          {/* Main Content */}
          <main className="md:col-span-3 space-y-8">
            {/* Query Input Section */}
            <Card className="p-6 bg-white shadow-lg rounded-xl border-0">
              <div className="space-y-4">
                <h2 className="text-xl font-bold text-gray-900">Ask a Question</h2>
                <Textarea
                  placeholder="Enter your query here..."
                  value={query}
                  onChange={(e) => setQuery(e.target.value)}
                  className="min-h-[120px] p-4 text-base bg-gray-50 border-gray-200 rounded-lg focus:ring-2 focus:ring-blue-500 resize-none transition-shadow duration-300 focus:shadow-lg"
                />
                <Button 
                  onClick={handleQuerySubmit}
                  disabled={isLoading || !query.trim()}
                  className="w-full bg-black hover:bg-gray-800 text-white py-6 rounded-lg text-lg font-semibold transition-colors duration-200"
                >
                  {isLoading ? 'Processing...' : 'Send Query'}
                </Button>
              </div>
            </Card>

            {/* Retrieved Documents Section */}
            {documents.length > 0 && (
              <Card className="p-6 bg-white shadow-lg rounded-xl border-0">
                <h2 className="text-xl font-bold mb-6 text-gray-900">Retrieved Documents</h2>
                <div className="grid gap-4">
                  {documents.map((doc, idx) => (
                    <div
                      key={idx}
                      className="p-4 bg-gray-50 rounded-lg hover:shadow-md transition-shadow duration-200"
                    >
                      <p className="text-gray-700 leading-relaxed">{doc}</p>
                    </div>
                  ))}
                </div>
              </Card>
            )}

            {/* Generated Answer Display */}
            {answer && (
              <Card className="p-6 bg-white shadow-lg rounded-xl border-0">
                <h2 className="text-xl font-bold mb-6 text-gray-900">Generated Answer</h2>
                <div className="bg-gray-50 p-6 rounded-lg">
                  <p className="text-gray-800 leading-relaxed text-lg">{answer}</p>
                </div>
              </Card>
            )}
          </main>
        </div>
      </div>

      {/* Error Dialog */}
      {error && (
        <AlertDialog>
          <AlertDialogTrigger asChild>
            <div className="fixed bottom-4 right-4">
              <div className="bg-red-100 border border-red-400 text-red-700 px-6 py-4 rounded-lg shadow-lg" role="alert">
                <span className="block sm:inline font-medium">{error}</span>
              </div>
            </div>
          </AlertDialogTrigger>
          <AlertDialogContent>
            <p className="text-red-600">{error}</p>
          </AlertDialogContent>
        </AlertDialog>
      )}
    </div>
  );
}
